.image-bar{
    background: url(../../Gallery/imageBar1.jpg);
    background-position-y: 50%;
    height: 31.4vw;
    background-size: cover;
    // filter: blur(4px);
    @media (max-width: 768px) {
        height: 37vw;
    }
    h2{
        color: #c7b299;
        font-size: 6vw;
    }
    p{
        color: #c7b299;
        text-align: center;
        font-size: 2vw;
    }
    .buttons{
        width: 100%;
        // margin: auto 0;
        border: 2px solid #c7b299;
        padding: 0.5vw 3vw;
        width: 50vw;
        @media (max-width: 768px) {
            font-size: 4vw;
        }
    }
    img{
        width: 100%;
        position: absolute;        
    }
}