.pin{width:30px;height:30px;border-radius:50% 50% 50% 0;background:#00cae9;position:absolute;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);left:50%;top:50%;margin:-20px 0 0 -20px}.pin:after{content:"";width:14px;height:14px;margin:8px 0 0 8px;background:#e6e6e6;position:absolute;border-radius:50%}.bounce{-webkit-animation-name:bounce;animation-name:bounce;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-duration:1s;animation-duration:1s}.pulse{background:#d6d4d4;border-radius:50%;height:14px;width:14px;position:absolute;left:50%;top:50%;margin:11px 0px 0px -12px;-webkit-transform:rotateX(55deg);transform:rotateX(55deg);z-index:-2}.pulse:after{content:"";border-radius:50%;height:40px;width:40px;position:absolute;margin:-13px 0 0 -13px;-webkit-animation:pulsate 1s ease-out;animation:pulsate 1s ease-out;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;opacity:0;box-shadow:0 0 1px 2px #00cae9;-webkit-animation-delay:1.1s;animation-delay:1.1s}@-webkit-keyframes pulsate{0%{-webkit-transform:scale(0.1, 0.1);transform:scale(0.1, 0.1);opacity:0}50%{opacity:1}100%{-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2);opacity:0}}@keyframes pulsate{0%{-webkit-transform:scale(0.1, 0.1);transform:scale(0.1, 0.1);opacity:0}50%{opacity:1}100%{-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2);opacity:0}}@-webkit-keyframes bounce{0%{opacity:0;-webkit-transform:translateY(-2000px) rotate(-45deg);transform:translateY(-2000px) rotate(-45deg)}60%{opacity:1;-webkit-transform:translateY(30px) rotate(-45deg);transform:translateY(30px) rotate(-45deg)}80%{-webkit-transform:translateY(-10px) rotate(-45deg);transform:translateY(-10px) rotate(-45deg)}100%{-webkit-transform:translateY(0) rotate(-45deg);transform:translateY(0) rotate(-45deg)}}@keyframes bounce{0%{opacity:0;-webkit-transform:translateY(-2000px) rotate(-45deg);transform:translateY(-2000px) rotate(-45deg)}60%{opacity:1;-webkit-transform:translateY(30px) rotate(-45deg);transform:translateY(30px) rotate(-45deg)}80%{-webkit-transform:translateY(-10px) rotate(-45deg);transform:translateY(-10px) rotate(-45deg)}100%{-webkit-transform:translateY(0) rotate(-45deg);transform:translateY(0) rotate(-45deg)}}

