@media (max-width : 576px) {
    
}
footer{
    position: relative;
    display: block;
}
.footer-container{
    background-color: #4d4d4d;
    border-top: 5px solid #c7b299;
    position: absolute;
    z-index: 1;
    top: 62vh;
    .main-logo{
        margin: 5vw 0;
        @media (max-width : 576px) {
            text-align: center;
        }
        svg{
            fill: #c7b299;
            @media (max-width : 576px) {
                width: 50vw;
            }
        }
    }
    h6{
        text-align: center;
        font-size: 3vw;
        margin: 2vw;
        color: #c7b299;
        @media (max-width : 576px) {
            font-size: 5vw;
        }
    }
    p{
        color: #c7b299;
        text-align: center;
        font-size: 1.5vw;
        @media (max-width : 576px) {
            font-size: 3vw;
        }
    }
    ul{
        display: flex;
        list-style: none;
        padding: 0 4vw;
        @media (max-width : 576px) {
            padding: 0 30vw;
        }
        li{
            font-size: 3vw;
            margin: auto;
            color: #c7b299;
            @media (max-width : 576px) {
                font-size: 5vw;
            }
        }
    }
    form{
        display: inline;
        text-align: center;
        label{
            top: 5vh;
            left: auto;
            margin-top: 4vh;
            position: relative;
            display: block;
            @media (max-width : 576px) {
                text-align: center;
                font-size: 3vw;
            }
        }
        .text-input{
            width: 80%;
            background-color: whitesmoke;
            border-radius: 2px;
            padding: 1px 5px;
            color: black;
            margin: 7vh 0;
            font-size: 1.6vw !important;
            @media (max-width : 576px) {
                font-size: 3vw !important;
                margin: 7vh 2vw;
            }
        }
        button{
            font-size: 1.8vw;
            // height: 4vh;
            margin: 2px;
            border-radius: 2px;
            border: none;
            background: none;
            color: #c7b299;
            @media (max-width : 576px) {
                font-size: 4vw;
            }
            :hover{
                color: blanchedalmond;
            }
        }
    }
}