.article-box{
    margin: 2vw;
    img{
        border: 1px solid black;
        display: block;
        height: 16vw;
        margin: 1vw 0;
    }
    h3{
        font-size: 2.2vw;
        display: inline-block;
        @media (max-width : 768px) {
            font-size: 4vw;
        }
    }
    h6{
        position: absolute;
        right: 3vw;
        top: 20vw;
        display: inline-block;
        @media (max-width : 768px) {
            display: none;
        }
    }
    p{
        border-top: 1px solid black;
        padding: 2vw 1vw 1vw;
        text-align: justify;
        font-size: 1.5vw;
        @media (max-width : 768px) {
            font-size: 2.2vw;
        }
    }
}