.card-view-1{
    box-shadow: 0 0 1vw 0;
    background-color: white;
    margin: 0 1vw;
    @media (max-width : 1200px){
        margin: 3vh 0;
    }
    img{
        width: 100%;
    }
    h3{
        margin: 1vw 1.5vw;
        @media  (min-width : 992px) and (max-width: 1200px){
            font-size: 2.7vw;
        }
        @media screen and (max-width : 990px){
            font-size: 3.4vw;
        }
    }
    p{
        margin: 0 1.5vw;
        text-align: justify;
        font-size: 16px;
        // @media (min-width : 992px) and (max-width: 1200px){
        //     font-size: 1.4vw;
        // }
        // @media screen and (max-width : 992px){
        //     font-size: 1.7vw;
        // }
    }
    h4{
        padding: 1vw;
        text-align: right;
        margin: 0;
        @media (min-width : 992px) and (max-width: 1200px){
            font-size: 2vw;
        }
        @media screen and (max-width :992px){
            font-size: 2.7vw;
        }
    }
}