.art-bar{
    .art-part{
        width: 100%;
        position: absolute;
        z-index: 1;
        img{
            width: 100%;
            height: 39vw;
            display: inline-block;
            margin: auto;
        }
    }
    .image-part{
        position: absolute;
        display: contents;
        img{
            width: 62vw;
            height: 39vw;
            display: block;
            z-index: 0;
            margin: auto;
        }
    }
    .title{
        position: absolute;
        z-index: 2;
        bottom: 6vw;
        left: 15vw;
        background-color: rgba(0, 0, 0, 0.349);
        color: #c7b299;
        padding: 1vw 2vw;
        font-size: 3vw;

    }
}